@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

.App-header {
  color: white;
  z-index: 1;
  position: relative;
  font-family: 'Bebas Neue', cursive;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.App-header h1{
  padding: .5em;
  margin-top: 0;
  text-align: left;
}

.App-header p{
  padding: .5em;
  margin-top: 0;
  text-align: right;
  font-size: 2em;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

@font-face {
  font-family: cuteFont;
  src: url('./img/Daydreamer.ttf');
}

h2 {
  font-family: cuteFont;
  margin-top: 1.5em;
  font-size: 7em;
  color: white;
}

.App-background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
